jQuery(function ($) {

    var $win = $(window);
    var $body = $('body');

    // DOM manipulations

    $('#mn_font').after($('#mn_uppercase_chooser')).after($('#mn_font_weight_chooser')).after($('#mn_alignment_chooser')).after($('#mn_font_size'))

    $('#mn_c_font').after($('#mn_c_trans_chooser')).after($('#mn_c_weight_chooser')).after($('#mn_c_fs'));

    $('#mn_c_fs').after('<span class="units" style="top:-16px">px</span><br>');
    $('#mn_font_size').after('<span class="units" style="top:-16px">px</span><br>');

    $('.mn_font').find('p:empty').remove();
    $('.mn_c_font').find('p:empty').remove();

    $('.switcher').each(function () {
        $(this).after('<div><div></div></div>')
    })

    $('.postbox select').each(function () {
        var id = this.id
        $(this).wrap('<div class="select-wrapper" id="' + id + '-wrapper"></div>')
    })

    $('[id*=mn_width_panel_]').each(function () {
        var ind = this.id.replace('mn_width_panel_', '');
        var cl = $('#mn_color_panel_' + ind);
        var scl = $('#mn_scolor_panel_' + ind);
        var bg = $('#mn_bg_color_panel_' + ind);
        var cc = $('#mn_chapter_' + ind);
        $(this).parent().append(bg).append(cl).append(scl).append(cc);
        bg.before('<span>Background</span>');
        cl.before('<span>Text</span>');
        scl.before('<span>Subheader</span>');
        cc.before('<span>Section</span>');
    })

    // Events

    $('#mn_sidebar_style').change(function () {
        var val = $(this).val();
        var $w = $(this).closest('.settings-form-row');

        $w.find('[data-notice]').hide().end().find('[data-notice=' + val + ']').show();
        if (val === 'static') {
            $('.mn_iconbar').show()
        } else {
            $('.mn_iconbar').hide()
        }

        if (val === 'full') {
            $('.mn_fade_content').hide();
            $('.mn_fade_full').show();
        } else {
            $('.mn_fade_content').show();
            $('.mn_fade_full').hide();
        }
    }).change();

    function validateLicense() {
        var $licenseCont = $('#mn_license');
        var valid = $('#mn_license_code', $licenseCont).length > 0 ? $('#mn_license_valid', $licenseCont).val() : '';
        var fname = $('#mn_license_fname', $licenseCont).val();
        var lname = $('#mn_license_lname', $licenseCont).val();
        var email = $('#mn_license_email', $licenseCont).val();
        var subscribe = $('#mn_license_subscribe', $licenseCont).prop('checked');
        var code = $('#mn_license_code', $licenseCont).length > 0 ? $('#mn_license_code', $licenseCont).val() : '';
        var licenseValid = true;
        var dfd = $.Deferred();

        if (valid) {
            dfd.resolve(valid);
            return dfd.promise();
        }

        $('#mn_license_email', $licenseCont).removeClass('validation-error');
        $('#mn_license_code', $licenseCont).removeClass('validation-error');
        $('.purchase-code .code-error', $licenseCont).hide();

        if (email != '') {
            if (!/^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/.test(email)) {
                $('#mn_license_email', $licenseCont).addClass('validation-error');
                licenseValid = false;
            } else {
                if($('#mn_license_code', $licenseCont).length === 0){
                    $('#mn_license_valid').val('1');
                }
                data = {
                    action: 'mn_subscribe_to_mailchimp_hidden',
                    list_id: 'ea7fe2df4a',
                    email: email,
                    fname: fname,
                    lname: lname
                };

                // Subscribe to hidden list (anyway)
                $.post(laim_localize.ajaxurl, data, function (response) {

                });
                if (subscribe) {
                    data = {
                        action: 'mn_subscribe_to_mailchimp',
                        list_id: 'bc51436a65',
                        email: email,
                        fname: fname,
                        lname: lname
                    };

                    // Subscribe to public list (only if checkbox selected)
                    $.post(laim_localize.ajaxurl, data, function (response) {

                    });
                }
            }
        }

        if (code != '') {
            var regexTest = /^[a-z0-9]+\-[a-z0-9]+\-[a-z0-9]+\-[a-z0-9]+\-[a-z0-9]+$/.test(code);

            if (!regexTest) {
                $('#mn_license_code', $licenseCont).addClass('validation-error');
                licenseValid = regexTest;
                dfd.resolve(licenseValid);
                $('#for_mn_license').trigger('click');
            } else {
                data = {
                    action: 'mn_validate_envato_license',
                    code: code
                };

                $.post(laim_localize.ajaxurl, data, function (response) {
                    if (response == -1) {
                        licenseValid = false;
                    } else {
                        if (response.error || response.item_id !== "8012790") {
                            licenseValid = false;
                            $('#mn_license_code', $licenseCont).addClass('validation-error');
                            $('.purchase-code .code-error', $licenseCont).show();
                            $('#for_mn_license').trigger('click');
                        } else {
                            $('#mn_license_valid', $licenseCont).val('1');
                        }
                    }
                    dfd.resolve(licenseValid);
                });
            }
        } else {
            dfd.resolve(licenseValid);
        }

        return dfd.promise();
    }

    $('#mn_license_code').on('input', function (e, options) {
        $('#mn_license_valid').val('');
        $(this).removeClass('validation-success');
    });

    $('#mn_license_email').on('input', function (e, options) {
        $('#mn_license_valid').val('');
    });

    $('#mn_license_subscribe').on('change', function (e, options) {
        $('#mn_license_valid').val('');
    });

    $('#mn-options-wrap form').on('submit', function (e, options) {
        /*options = options || {};
        if (!options.flag) {
            e.preventDefault();
        }*/

        var $form = $(this);
        var p = $('.mn_display');
        var current = p.find('.loc_popup')
        var hidden = p.find('input:hidden');
        var user = current.find('select[id*=user_status]').val();
        var rule = current.find('select[id*=display_rule]').val();
        var desktop = current.find('select[id*=display_desktop]').val();
        var mobile = current.find('select[id*=display_mobile]').val();
        var ids = current.find('[id*=display_ids]').val();

        var resulted = {
            'user': {
                'everyone': user === 'everyone' ? 1 : 0,
                'loggedin': user === 'loggedin' ? 1 : 0,
                'loggedout': user === 'loggedout' ? 1 : 0
            },
            'desktop': {
                'yes': desktop === 'yes' ? 1 : 0,
                'no': desktop === 'no' ? 1 : 0
            },
            'mobile': {
                'yes': mobile === 'yes' ? 1 : 0,
                'no': mobile === 'no' ? 1 : 0
            },

            'rule': {
                // 'include': rule === 'include' ? 1 : 0,
                // 'exclude': rule === 'exclude' ? 1 : 0
                'include': 0,
                'exclude': 1
            },
            'location': {
                'pages': traversePages(current.find('input[id*=display_page]')),
                'cposts': traversePages(current.find('input[id*=display_cpost]')),
                'cats': traversePages(current.find('input[id*=display_cat]')),
                'taxes': {},
                'langs': traversePages(current.find('input[id*=display_lang]')),
                'wp_pages': traversePages(current.find('input[id*=display_wp_page]')),
                'ids': ids.split(',')
            }
        };
        hidden.val(JSON.stringify(resulted));
        showLoadingView();

        /*if (!options.flag) {
            validateLicense().done(function (data) {
                if (data) {
                    showLoadingView();
                    $form.trigger('submit', {flag: true});
                } else {
                    hideLoadingView();
                    e.preventDefault();
                }
            });
        }*/
    })

    function traversePages(pages) {
        var res = {};

        pages.each(function (i, el) {
            var t = $(el);
            var val = t.val();
            if (t.is(':checked')) res[val] = 1;
        });

        return res
    }

    function showLoadingView() {
        $('#fade-overlay').addClass('loading');
    }

    function hideLoadingView() {
        $('#fade-overlay').removeClass('loading');
    }


    function isScrolledIntoView($elem, elemTop, elemBottom, rule) {
        var docViewTop = $win.scrollTop();
        var docViewBottom = docViewTop + $win.height();

        return rule === 'after' ? docViewBottom > elemBottom + 50 : (elemBottom <= docViewBottom && elemTop >= docViewTop - 25) || (elemBottom > docViewBottom && elemTop < docViewTop - 25);
    }

    var state = 'in';
    var $tabs = $('#tabs-copy');
    var $subtabs = $('#mn_appearance_tabs');
    var $or = $('#tabs');
    var $form = $('#mn-options-wrap form')

    $win.scroll(function () {

        var elemTop = $tabs.offset().top;
        var elemBottom = elemTop + $tabs.height();
        if (isScrolledIntoView($tabs, elemTop, elemBottom, 'in')) {
            if (state !== 'in') {
                state = 'in';
                $or.removeClass('transition-in');
                //setTimeout(function () {
                $body.removeClass('fixed-nav');
                $or.css({'width': '', left: ''});
                //}, 50)
            }
        } else {
            if (state !== 'out') {
                state = 'out';
                $body.addClass('fixed-nav');
                $or.css({'width': $tabs.width(), left: $form.offset().left});
                $subtabs.css({'width': $tabs.width(), left: $form.offset().left});
                //setTimeout(function () {
                $or.addClass('transition-in');
                //}, 100)
            }
        }

    });

    $win.resize(function () {
        if ($body.is('.fixed-nav')) {
            $or.css({'width': $tabs.width(), left: $form.offset().left})
        }
    })

    $or.find('li').not('#save-tab').click(function () {
        $('html,body').animate({
            scrollTop: 0
        }, 300);
    });

    $subtabs.click(function () {
        $('html,body').animate({
            scrollTop: 0
        }, 300);
    });

    $('#save-tab').click(function () {
        $(this).closest('form').submit();
    })

    $('#form-tab').click(function () {

    })

    var file_frame, attachment, $input;

    $('.mn-choose-image').click(function () {
        var $t = $(this);
        $input = $t.parents('.settings-form-row').eq(0).find('input');
        if (file_frame) {
            file_frame.open();
            return;
        }

        // Create the media frame.
        file_frame = wp.media.frames.file_frame = wp.media({
            multiple: false  // Set to true to allow multiple files to be selected
        });

        // When an image is selected, run a callback.
        file_frame.on('select', function () {
            // We set multiple to false so only get one image from the uploader

            attachment = file_frame.state().get('selection').first().toJSON();
            $('.image-preview').html('<img src="' + attachment.url + '"/>');
            if (!$('.mn-remove-image').length) $t.parent().after("<div><span class='mn-remove-image'>Remove Image</span></div>");
            $input.val(attachment.sizes.full.url);
            $input.closest('form').submit()
//      $currBtn = null;
        });

        // Finally, open the modal
        file_frame.open();
    })

    $(document).on('click', '.mn-remove-image', function () {
        var $t = $(this);
        $input = $t.parents('.settings-form-row').eq(0).find('input');
        $input.val('');
        $(this).closest('form').submit()
    })


    // multiple menus

    var $select = $('#mn-menu__select-wrapper ');
    var $popup, $locPopup, $clone, $def;
    var $storageInput = $('.mn_active_menu input');
    var $list = $('.mn-menu__list');

    // dom
    $select.wrap('<div class="mn-menu__select_popup"></div>');
    $popup = $('.mn-menu__select_popup');
    $popup.append('<span class="mn-menu__btn mn-menu__btn__ok"><i class="flaticon-plus"></i></span><span class="mn-menu__btn mn-menu__btn__close"><i class="flaticon-cross"></i></span>');

    $clone = $('.loc_popup > div').clone();

    $clone.find('.rule_lang').remove();

    $clone.find('[id]').each(function () {
        var id = $(this).attr('id');
        $(this).attr('id', id + '_popup')
    });
    $clone.find('[for]').each(function () {
        var _for = $(this).attr('for');
        $(this).attr('for', _for + '_popup')
    });

    $clone.addClass('mn-menu__loc_popup').prepend('\
        <div class="mn-menu__def_wrapper"><p>Mark pages of your website to display this menu.</p>\
        <p>\
            <input class="checkbox" type="checkbox" value="yes" id="display_default_menu_popup" name="display_default_menu">\
            <label for="display_default_menu_popup">Display everywhere by default. / General display rules may overwrite this setting</label>\
        </p>\
        </div>\
    ');

    $('.dw_pages_wrap', $clone).prepend('\
    <div><span class="mn-menu__loc_btn_check_all">Check All</span> <span class="mn-menu__loc_btn_check_none">Uncheck All</span></div>\
    ')

    $clone.addClass('mn-menu__loc_popup').append('<span class="mn-menu__loc_btn_save">Save</span> <span class="mn-menu__loc_btn_close">Close</span>')

    $(document).on('click', '.mn-menu__loc_btn_check_all', function () {
        $('.mn-menu__loc_popup input[type="checkbox"][name!="display_default_menu"]').prop('checked', true);
    });
    $(document).on('click', '.mn-menu__loc_btn_check_none', function () {
        $('.mn-menu__loc_popup input[type="checkbox"][name!="display_default_menu"]').prop('checked', false);
    });
    $(document).on('change', '[name="display_default_menu"]', function (e) {
        $('.mn-menu__loc_popup .dw_pages_wrap').show();
        if($(this).prop('checked'))
            $('.mn-menu__loc_popup .dw_pages_wrap').hide();
    });

    $popup.after($clone);

    $locPopup = $('.mn-menu__loc_popup');
    $def = $locPopup.find('#display_default_menu_popup');

    $locPopup.find('#display_ids_popup').parent().hide();

    $list.find('.mn-menu__item').each(function () {
        var $t = $(this);
        var id = $t.data('id');
        var location;
        var curr = window.mn_menus[id]
        if (curr) {
            location = curr['loc'];
            $t.data('location', location);
            $t.data('isDef', curr['isDef']);
        }
    })

    $('.mn-menu__add').click(function () {
        $(this).closest('.mn_active_menu').addClass('mn_active_menu--selecting');
    })

    $('.mn-menu__btn__ok').click(function () {
        var val = $select.find('select').val();
        var $selected = $select.find(':selected');

        var $item;
        console.log(val);

        // check if added
        if ($list.find('[data-id=' + val + ']').length) {
            alert('Already added!');
            return;
        }

        $item = $('<li class="mn-menu__item" data-id="' + val + '">' + $selected.text() + '<i class="flaticon-cross"></i></li>');
        $list.append($item);


        updateMenuMap();

        if ($list.find('.mn-menu__item').length === 1) { // single item
            openLocPopup($item);
            $locPopup.find(':checkbox').prop('checked', true)
            $locPopup.find('.mn-menu__loc_btn_save').click();
            closeLocPopup({target: null}, true);
            return;
        }
        $(this).closest('form').submit()


    })

    $('.mn-menu__btn__close').click(function () {
        $(this).closest('.mn_active_menu').removeClass('mn_active_menu--selecting');
    })

    $list.on('click', '.mn-menu__item', function (e) {
        var $t = $(e.target);
        var $form = $t.closest('form');

        if ($t.is('.flaticon-cross')) {
            $t.closest('.mn-menu__item').remove();
            updateMenuMap();
            $form.closest('form').submit();
            return;
        }

        console.log('open loc popup');
        openLocPopup($t.closest('.mn-menu__item'))
    })

    $locPopup.on('click', '.mn-menu__loc_btn_save', function () {
        var current = $locPopup;
        var ids = current.find('[id*=display_ids]').val();
        var $item = $locPopup.data('currentItem');

        var location = {
            'pages': traversePages(current.find('input[id*=display_page]')),
            'cposts': traversePages(current.find('input[id*=display_cpost]')),
            'cats': traversePages(current.find('input[id*=display_cat]')),
            'taxes': {},
            'langs': traversePages(current.find('input[id*=display_lang]')),
            'wp_pages': traversePages(current.find('input[id*=display_wp_page]')),
            'ids': ids.split(',')
        };

        $item.data('location', location);
        $item.data('isDef', $def.is(':checked'));

        updateMenuMap();

        current.closest('form').submit()

    });

    $def.on('change', function (e) {
        var $curr;

        // check if already default is set
        $list.find('.mn-menu__item').each(function () {
            var $t = $(this);
            if ($t.data('isDef')) {
                $curr = $t;
            }
        });

        if ($curr && $curr.data('id') != $locPopup.data('currentItem').data('id')) {
            $def.attr('checked', false);
            alert('You already have default menu. Please edit this menu before and uncheck this property.');
            return false;
        }
    })

    $locPopup.on('click', '.mn-menu__loc_btn_close', closeLocPopup)

    function updateMenuMap() {
        var $items = $list.find('.mn-menu__item');
        var map = {};
        $items.each(function () {
            var $t = $(this);
            var locationData = $t.data('location') || {};

            map[$t.data('id')] = {
                term_id: $t.data('id'),
                name: $.trim($t.text()),
                loc: locationData,
                isDef: $t.data('isDef')
            }
        })
        $storageInput.val(JSON.stringify(map));
    }

    function openLocPopup($item) {
        // attempt to retrieve date
        var locationData = $item.data('location');
        $locPopup.data('currentItem', $item);

        // reset
        $locPopup.find(':checked').prop('checked', false)
        $locPopup.find(':text').val('');

        if (locationData) {
            populatePopup(locationData, $item);
        }

        $def.prop('checked', $item.data('isDef'))
        if($item.data('isDef')){
            $('.dw_pages_wrap', $locPopup).hide();
        }else{
            $('.dw_pages_wrap', $locPopup).show();
        }

        setTimeout(function () {
            $body.on('click', closeLocPopup)
        }, 0)


        $item.closest('.mn_active_menu').addClass('mn_active_menu--editing')
    }

    function closeLocPopup(e, force) {
        var $t = $(e.target);
        if (!$t.closest($locPopup).length || $t.is('.mn-menu__loc_btn_close') || force) {
            $('.mn_active_menu').removeClass('mn_active_menu--editing');
            $body.off('click', closeLocPopup)
        }
    }

    function populatePopup(location, current) {
        var prop, cat;

        for (prop in location) {
            cat = location[prop];
            for (var name in cat) {
                $locPopup.find('#display_' + prop.replace(/s$/, '') + '_' + name + '_popup').prop('checked', location[prop]);
            }
        }

        //$locPopup.find('#display_ids_popup').val(location.ids.join(','));
    }

    $body.addClass('page-loaded');

})